
html, body {
    margin: 0;

    max-width: 100%;
    min-height: 100%;
    padding: 0;
}

body {
    position: relative;
    
    will-change: scroll-position;
}

* {
    box-sizing: border-box;
}

*:after, *:before {
    box-sizing: inherit;
}

/*====================================================================*/
/*------------------------- RESET + CLEARFIX -------------------------*/
/*====================================================================*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, font, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-weight: inherit;
    font-style: inherit;
    font-size: 100%;
    font-family: inherit;
    vertical-align: baseline;
}
:focus {
    outline: 0;
}
body {
    line-height: 1;
    color: black;
    background: white;
}
ol, ul {
    list-style: none;
}
table {
    border-collapse: separate;
    border-spacing: 0;
}
caption, th, td {
    text-align: left;
    font-weight: normal;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: "";
}
blockquote, q {
    quotes: " ";
}

.cf:after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
    }
.cf { display: inline-table; }
* html .cf { height: 1%; }
.cf { display: block; }

:root {
    --P_PageSection__dragged_height: 40px;
    --P_PageSection_sorting-transition-duration: 0.35s;
    --P_PageSection_sorting-transition-easing: ease-out;

    --P_PageSection--placeholder_text: "Umístit sekci zde...";
    --P_PageSection--empty_text: "Sem můžete přidat další sekci...";
}

.medium-editor-toolbar *:not(input)  {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.medium-editor-toolbar * {
    pointer-events: all;
}

/*--------------------------------------------------------------------*/
/*------------------------- TOOLBAR WRAPPER --------------------------*/
/*--------------------------------------------------------------------*/

.medium-editor-hidden {
    display: none;
}

.medium-editor-toolbar {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 16px;
    
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;

    height: 160px;
    width: 240px;

    opacity: 0;

    display: flex;
    justify-content: center;
    align-items: flex-end;

    pointer-events: none;
    visibility: hidden;

    will-change: opacity, top, left, transform;
    transition:
        transform 0.2s ease-out 0.15s,
        z-index 0s linear 0.2s,
        visibility 0s linear 0.2s,
        opacity 0.2s ease-in-out 0s;
}

.medium-toolbar-arrow-over.medium-editor-toolbar {
    margin-top: -6px;

    align-items: flex-start;
}

.medium-editor-toolbar-active {
    opacity: 1;
    z-index: 11000;

    visibility: visible;
    transition:
        top 0.2s ease-in-out 0s,
        left 0.2s ease-in-out 0s,
        bottom 0.2s ease-in-out 0s,
        right 0.2s ease-in-out 0s,
        z-index 0s linear 0s,
        visibility 0s linear 0s,
        transform 0.2s ease-out 0.15s,
        opacity 0.2s ease-in-out 0s;
}

.medium-editor-toolbar-active.medium-editor-first-show {
    transition:
        z-index 0s linear 0s,
        visibility 0s linear 0s,
        transform 0.2s ease-out 0.15s,
        opacity 0.2s ease-in-out 0s;
}

.medium-editor-toolbar.medium-editor-stalker-toolbar {
    animation-name: medium-editor-move-toolbar-down;
    animation-duration: 0.2s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
}

.medium-editor-toolbar-active.medium-editor-stalker-toolbar {
    animation-name: medium-editor-move-toolbar-up;
}

.medium-toolbar-arrow-over.medium-editor-toolbar.medium-editor-stalker-toolbar {
    animation-name: medium-editor-move-toolbar-up;
}

.medium-toolbar-arrow-over.medium-editor-toolbar-active.medium-editor-stalker-toolbar {
    animation-name: medium-editor-move-toolbar-down;
}

/*--------------------------------------------------------------------*/
/*-------------------------- TOOLBAR ACTIONS -------------------------*/
/*--------------------------------------------------------------------*/

.medium-editor-toolbar ul {
    text-align: center;

    position: relative;
    margin: 0;

    min-width: 120px;
    height: 43px;
    padding: 0;

    background-color: #f5f5f5;
    border-top: 3px solid #000;
    border-bottom: 0px solid #000;
    border-radius: 4px;
    box-shadow: 0px -4px 15px rgba(0, 0, 0, 0.5);

    display: flex;

    will-change: width, height, transform;
}

.medium-toolbar-arrow-under.medium-editor-toolbar ul {
    border-top-width: 0px;
    border-bottom-width: 3px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5);
}

.medium-editor-toolbar .medium-editor-default-actions-hidden {
    min-width: 100%;
    height: 100%;

    transition:
        border 0.2s ease-in-out 0s,
        min-width 0.2s cubic-bezier(0.5, 1, 1, 1) 0s,
        height 0.2s linear 0.4s;
}

.medium-editor-left-edge ul {
    text-align: left;
}

.medium-editor-right-edge ul {
    text-align: right;
}

.medium-editor-toolbar .medium-editor-default-actions-displayed {
    min-width: 120px;
    height: 43px;

    transition:
        min-width 0.2s cubic-bezier(0, 0, .5, 0) 0.4s,
        height 0.2s linear 0s;
}

.medium-editor-toolbar .medium-editor-before-fill,
.medium-editor-toolbar .medium-editor-after-fill {
    flex-grow: 1;

    transition: all 0.2s ease-in-out 0s;
}

.medium-editor-left-edge .medium-editor-before-fill,
.medium-editor-right-edge .medium-editor-after-fill {
    flex-grow: 0;
}

.medium-editor-toolbar.medium-editor-first-show .medium-editor-before-fill,
.medium-editor-toolbar.medium-editor-first-show .medium-editor-after-fill {
    transition: none;
}

/*---------------------------- ARROW ----------------------------*/

.medium-toolbar-arrow-over ul:before,
.medium-toolbar-arrow-under ul:after {
    content: "";

    position: absolute;
    left: 50%;
    margin-left: -8px;

    height: 0;
    width: 0;

    border-style: solid;
    border-width: 8px;
}

.medium-toolbar-arrow-under ul:after {
    bottom: -19px;

    border-color: black transparent transparent transparent;
}

.medium-toolbar-arrow-over ul:before {
    top: -19px;

    border-color: transparent transparent black transparent;
}

/*---------------------------- BUTTONS ----------------------------*/

.medium-editor-toolbar ul li {
    margin: 0;

    width: 40px;
    height: 40px;
    padding: 0;

    list-style: none;

    overflow: hidden;
    display: inline-flex;
    
    transition: 
        border 0.2s ease-in-out 0s,
        width 0.2s ease-in-out 0s,
        height 0.2s ease-in-out 0s;
}

.medium-editor-toolbar li:first-of-type {
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
}

.medium-editor-toolbar li:last-of-type  {
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
}

.medium-editor-toolbar li button {
    cursor: pointer;

    font-size: 14px;
    line-height: 40px;

    padding: 0;
    width: 40px;
    min-width: 40px;
    height: 40px;

    background-color: #f5f5f5;
    border-radius: 0;
    border: none;
    opacity: 1;

    will-change: opacity;
}

.medium-editor-toolbar li:first-of-type button {
    border-top-left-radius: 4px;
}

/*.medium-editor-toolbar li:nth-last-of-type(2) button,*/
.medium-editor-toolbar li:last-of-type button {
    border-top-right-radius: 4px;
}

.medium-editor-toolbar.medium-toolbar-arrow-over li:first-of-type button {
    border-top-left-radius: 0px;
    border-bottom-left-radius: 4px;
}

.medium-editor-toolbar.medium-toolbar-arrow-over li:last-of-type button {
    border-top-right-radius: 0px;
    border-bottom-right-radius: 4px;
}

.medium-editor-toolbar:not(.medium-editor-first-show) .medium-editor-default-actions-hidden li button {
    opacity: 0;

    transition: 
        opacity 0.2s linear 0s,
        color 0.2s ease-in-out 0s,
        background 0.2s ease-in-out 0s;
}

.medium-editor-toolbar:not(.medium-editor-first-show) .medium-editor-default-actions-displayed li button {
    opacity: 1;

    transition: 
        opacity 0.2s linear 0.6s,
        color 0.2s ease-in-out 0s,
        background 0.2s ease-in-out 0s;
}

.medium-editor-toolbar li button:focus {
    outline: 0;
}

.medium-editor-toolbar ul li .medium-editor-button-active {
    color: #fff;
    background-color: #fba742;
}

.medium-editor-action-bold {
    font-weight: bolder;
}

.medium-editor-action-italic {
    font-style: italic;
}

