.medium-editor-toolbar li .medium-editor-action-underline {
    text-decoration: underline;
}

.medium-editor-toolbar li .medium-editor-action-pre {
    font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
    font-size: 12px;
    font-weight: 100;

    padding: 15px 0;
}

/*--------------------------------------------------------------------*/
/*------------------------------- FORM -------------------------------*/
/*--------------------------------------------------------------------*/

.medium-editor-toolbar-form {
    z-index: -1;
    position: absolute;
    bottom: 0px;
    left: 0;
    right: 0;
    margin: auto;

    width: 100%;
    height: 40px;

    background-color: #f5f5f5;
    border-radius: 4px;
    border-bottom: 3px solid #000;
    border-top: 0px solid #000;
    opacity: 0;

    pointer-events: none;
    overflow: hidden;

    will-change: opacity, width, height;
    transition:
        z-index 0s linear 0.4s,
        visibility 0s linear 0.4s,
        opacity 0.2s linear 0.2s,
        height 0.2s linear 0s;
}

.medium-toolbar-arrow-over .medium-editor-toolbar-form {
    bottom: auto;
    top: 0;

    border-top-width: 3px;
    border-bottom-width: 0;
}

.medium-editor-action-createLink-displayed.medium-editor-toolbar-form {
    z-index: 1;

    height: 100%;

    opacity: 1;

    pointer-events: all;
    overflow: hidden;

    transition:
        z-index 0s linear 0.2s,
        visibility 0s linear 0.2s,
        opacity 0.2s linear 0.2s,
        height 0.2s linear 0.4s;
}

.medium-editor-toolbar-form a,
.medium-editor-toolbar-form input {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.medium-editor-toolbar-form .medium-editor-toolbar-form-row {
    line-height: 14px;

    margin-left: 5px;

    padding-bottom: 5px;
}

.medium-editor-toolbar-form .medium-editor-toolbar-input,
.medium-editor-toolbar-form label {
    font-size: 14px;

    margin: 0;

    padding: 6px;
    width: 100%;

    border: none;

    display: inline-block;
}

.medium-editor-toolbar-form .medium-editor-toolbar-input:focus,
.medium-editor-toolbar-form label:focus {
    border: none;
    box-shadow: none;
    outline: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.medium-editor-toolbar-form a {
    font-size: 24px;
    text-decoration: none;
    font-weight: bolder;

    margin: 0 10px;

    display: inline-block;
}

/*--------------------------------------------------------------------*/
/*----------------------------- CONTENT ------------------------------*/
/*--------------------------------------------------------------------*/

[data-medium-editor-element] sub {
    vertical-align: sub
}

[data-medium-editor-element] sup {
    vertical-align: super
}

/*--------------------------------------------------------------------*/
/*-------------------------- ANCHOR PREVIEW --------------------------*/
/*--------------------------------------------------------------------*/

.medium-editor-anchor-preview {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 16px;

    text-align: center;
    line-height: 1.4;
    word-break: break-all;
    word-wrap: break-word;

    z-index: 2000;
    position: absolute;
    left: 0;
    top: 0;

    max-width: 280px;

    visibility: hidden;
}

.medium-editor-anchor-preview a {
    margin: 5px 5px 10px;

    color: #fff;

    display: inline-block;
}

.medium-editor-anchor-preview-active {
    visibility: visible;
}

/*--------------------------------------------------------------------*/
/*---------------------------- PLACEHOLDER ---------------------------*/
/*--------------------------------------------------------------------*/

.medium-editor-placeholder {
    position: relative;
}

.medium-editor-placeholder:after {
    content: attr(data-placeholder) !important;

    font-style: italic;
    white-space: pre;

    position: absolute;
    left: 0;
    top: 0;
    margin: inherit;

    padding: inherit;
}

